.announcement{
    margin: 4rem 6rem;
    color: black ;
}
.announcement h1{
    font-size: 3rem;
}

.card{
    margin-top: 3rem;
    display:inline-flex;
    justify-content: space-between;
    align-items: center;
}
.t-card{
    width: 70%;
    text-align: start;
    box-shadow: 0 5px 25px 2px rgba(0, 0, 0, 0.11);
    border-radius: 7px;
    cursor: pointer;
    padding: 1rem .8rem;
}

.t-image{
    height: 200px;
    overflow: hidden;
    border-radius: 7px;
}
.t-image:hover img{
width: 100%;
height: 100%;
transition: 0.3s ease-in-out;
border-radius: 7px;
}
.t-image:hover img{
    transform: scale(1.3);
}

.t-card h4{
    font-size: 1.3rem;
    padding: 0.9rm 0 .2rem 0;
}

@media screen and (max-width:850px){
    .announcement{
        margin: 4rem 2rem;
    }
    .card{
        flex-direction: column;
    }

    .t-card{
        width: 100%;
        margin-bottom: 1.5rem; 
    }
    .t-image{
        height: 300px;
        overflow: hidden;
        border-radius: 7px;
    }
}



